<template>
  <div class="malfunction">
    <title-header title="操作日志"></title-header>
    <div class="selects">
      <div class="select-titles">
        <el-row :gutter="24">
          <el-col :span="5"><div class="common-item">资源类型</div></el-col>
          <el-col :span="5"><div class="common-item">操作类型</div></el-col>
          <el-col :span="5"><div class="common-item">资源主标识</div></el-col>
          <el-col :span="5"><div class="common-item">备注</div></el-col>
          <el-col :span="4"><div class="common-item"></div></el-col>
        </el-row>
      </div>
      <div class="select-content">
        <el-row :gutter="24">
          <el-col :span="5">
            <div class="common-item selsct-common">
              <el-select v-model="queryFilter.resourceType" placeholder="请选择资源类型">
                <el-option
                  v-for="item in resourceTypes"
                  :key="item.display"
                  :label="item.display"
                  :value="item.code">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="common-item selsct-common">
              <el-select v-model="queryFilter.operation" placeholder="请选择操作类型">
                <el-option
                  v-for="item in operationTypes"
                  :key="item.display"
                  :label="item.display"
                  :value="item.code">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="common-item selsct-common">
              <el-input v-model="queryFilter.resourceKey" placeholder="请输入资源主标识"></el-input>
            </div>
          </el-col>
          <el-col :span="5">
            <div class="common-item selsct-common">
              <el-input v-model="queryFilter.memo" placeholder="请输入备注"></el-input>
            </div>
          </el-col>
          <el-col :span="4">
            <el-button class="blue" type="primary" @click="handleSearch">查询</el-button>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-resource_type" slot-scope="scope"><p>{{resourceType(scope.row.resource_type)}}</p></div>
        <div slot="col-operation" slot-scope="scope"><p>{{operationType(scope.row.operation)}}</p></div>
        <div slot="col-create_time" slot-scope="scope"><p>{{calcTime(scope.row.create_time)}}</p></div>
      </iot-table>
    </div>
  </div>
</template>

<script>
import { operations } from '@/api/system'
import dateUtil from '@/utils/dateUtil'
import iotTable from '@/components/iotTable'
import TitleHeader from '@/components/titleHeader'
import msgUtil from '@/utils/msgUtil'

export default {
  name: 'Malfunction',
  components: {
    iotTable,
    TitleHeader
  },
  mounted() {
    this.initData()
  },
  data() {
    return {
      loading: false,
      queryFilter: {
        resourceType: '',
        operation: '',
        resourceKey: '',
        memo: ''
      },
      operationTypes: [
        {
          code: 'C',
          display: '新增'
        },
        {
          code: 'U',
          display: '更新'
        },
        {
          code: 'D',
          display: '删除'
        }
      ],
      resourceTypes: [
        {
          code: 'CO',
          display: '公司'
        },
        {
          code: 'MB',
          display: '账号'
        },
        {
          code: 'PJ',
          display: '项目'
        },
        {
          code: 'DV',
          display: '设备'
        }
      ],
      project: {
        name: '',
        id: ''
      },
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'resource_type', label: '资源类型', type: 'slot' },
        { prop: 'resource_key', label: '资源主标识' },
        { prop: 'operation', label: '操作类型', type: 'slot' },
        { prop: 'mid', label: '操作账号' },
        { prop: 'memo', label: '备注' },
        { prop: 'create_time', label: '操作时间', type: 'slot' }
      ]
    }
  },
  computed: {
    // 报警类型
    resourceType() {
      return (resource_type) => {
        if (resource_type === 'CO') {
          return '公司'
        } else if (resource_type === 'MB') {
          return '账号'
        } else if (resource_type === 'PJ') {
          return '项目'
        } else if (resource_type === 'DV') {
          return '设备'
        } else {
          return ''
        }
      }
    },
    operationType() {
      return (operation) => {
        if (operation === 'C') {
          return '新增'
        } else if (operation === 'U') {
          return '更新'
        } else if (operation === 'D') {
          return '删除'
        } else {
          return ''
        }
      }
    },
    // 时间
    calcTime() {
      return (time) => {
        return time ? dateUtil.format(new Date(time * 1000), 'YYYY-MM-dd HH:mm:ss') : ''
      }
    }
  },
  methods: {
    handleSearch() {
      if (this.queryFilter.resourceType !== '' || this.queryFilter.operation !== '' || this.queryFilter.resourceKey !== '' || this.queryFilter.memo !== '') {
        this.initData()
      } else {
        msgUtil.error('查询条件不能为空')
      }
    },
    async initData() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        resourceType: this.queryFilter.resourceType ? this.queryFilter.resourceType : undefined,
        operation: this.queryFilter.operation ? this.queryFilter.operation : undefined,
        resourceKey: this.queryFilter.resourceKey ? this.queryFilter.resourceKey : undefined,
        memo: this.queryFilter.memo ? this.queryFilter.memo : undefined
      }
      try {
        const { data } = await operations(params)
        this.tableData = data.items
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
        this.loading = false
      } catch (error) {
        this.loading = false
        msgUtil.error('查询数据失败')
      }
    }
  }
}
</script>

