import constant from '@/common/constant'
import http from '@/utils/http'
import { getItem } from '@/utils/funcs'

export function getAuthlist(query) {
  return http.get(`${constant.baseUrl}/v2/companies`, query)
}

export function findList() {
  return http.get(`${constant.baseUrl}/v2/roles`)
}


export function del(code) {
  return http.del(`${constant.baseUrl}/v2/role/${code}`)
}

export function findUsers(params) {
  return http.get(`${constant.baseUrl}/v2/members?page=${params.page || 1}&per_page=${params.per_page || 10}`, params)
}


export function delUsernew(mid, verifycode) {
  return http.del(`${constant.baseUrl}/v3/member?mid=` + mid + `&verify_code=` + verifycode)
}

export function operations(params) {
  return http.get(`${constant.baseUrl}/v2/operations`, params)
}

export function customInfo(params) {
  return http.post(`${constant.baseUrl}/v2/company/logo/custom`, params)
}


export function subescel(data) {
  return http.post(`${constant.baseUrl}/v2/imei/mac/excel`, data, {
    headers: { 'X-API-TOKEN': getItem(constant.token) }
  })
}

